import { init } from 'ityped';
import { useEffect, useRef } from 'react';
import './intro.scss';

export default function Intro() {
  const textRef = useRef();
  useEffect(() => {
    init(textRef.current, {
      showCursor: false,
      backDelay: 1500,
      backSpeed: 60,
      showCursor: true,
      strings: ['Android', 'React', 'Competitive Programmer'],
    });
  }, []);
  return (
    <div className='intro' id='intro'>
      <div className='left'>
        <div className='imageContainer'>
          <img src='assets/face.gif' alt='' />
        </div>
      </div>
      <div className='right'>
        <div className='wrapper'>
          <h2>Hi There , I'm</h2>
          <h1>Malathi Dharmalingam</h1>
          <h3>
            Software Engineer <span ref={textRef}></span>
          </h3>
        </div>
        <a href='#portfolio'>
          <img src='assets/down.png' alt=''></img>
        </a>
      </div>
    </div>
  );
}
