import { useEffect, useState } from 'react';
import {
  designPortfolio,
  featuredPortfolio,
  mobilePortfolio,
  testingPortfolio,
  webPortfolio,
} from '../../data';
import PortfolioList from '../portfolioList/PortfolioList';
import './portfolio.scss';

export default function Portfolio() {
  const [selected, setSelected] = useState('featured');
  const [data, setData] = useState([]);

  useEffect(() => {
    switch (selected) {
      case 'featured':
        setData(featuredPortfolio);
        break;
      case 'react':
        setData(webPortfolio);
        break;
      case 'mobile':
        setData(mobilePortfolio);
        break;
      case 'design':
        setData(designPortfolio);
        break;
      case 'testing':
        setData(testingPortfolio);
        break;
      default:
        setData(featuredPortfolio);
    }
  }, [selected]);

  const list = [
    {
      id: 'featured',
      title: 'Featured',
    },
    {
      id: 'mobile',
      title: 'Mobile App',
    },
    {
      id: 'react',
      title: 'React App',
    },
    {
      id: 'testing',
      title: 'Web Testing',
    },
    {
      id: 'design',
      title: 'Design',
    },
  ];

  return (
    <div className='portfolio' id='portfolio'>
      <h1>Portfolio</h1>

      <ul>
        {list.map((item) => (
          <PortfolioList
            title={item.title}
            active={selected == item.id}
            setSelected={setSelected}
            id={item.id}
          />
        ))}
      </ul>
      <div className='container'>
        {data.map((d) => (
          <div className='item'>
            <img src={d.img} alt='' />
            <h3>{d.title}</h3>
          </div>
        ))}
      </div>
    </div>
  );
}
